@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@1&family=Skranji&display=swap');

.about {
  margin-top: 10%;

  .title {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 2rem;
    font-family: 'DM Serif Display', serif;
  }

  .content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .text {
    flex-basis: 50%;
    font-family: 'PT Serif', serif;
    font-size: 25px;
    margin: 0auto;
    color: #fff;
  }

  .image {
    flex-basis: 30%;
    display: flex;
    justify-content: center;
  }

  .image img {
    width: 300px;
    height: 300px;
    object-fit: cover;
    border-radius: 40%;
  }

  .puff-in-center {
    -webkit-animation: puff-in-center 0.7s cubic-bezier(0.47, 0, 0.745, 0.715)
      both;
    animation: puff-in-center 0.7s cubic-bezier(0.47, 0, 0.745, 0.715) both;
  }

  /* ----------------------------------------------
 * Generated by Animista on 2023-5-3 9:22:52
 * Licensed under FreeBSD License.
 * See http://animista.net/license for more info. 
 * w: http://animista.net, t: @cssanimista
 * ---------------------------------------------- */

  /**
 * ----------------------------------------
 * animation puff-in-center
 * ----------------------------------------
 */
  @-webkit-keyframes puff-in-center {
    0% {
      -webkit-transform: scale(2);
      transform: scale(2);
      -webkit-filter: blur(4px);
      filter: blur(4px);
      opacity: 0;
    }

    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
      -webkit-filter: blur(0px);
      filter: blur(0px);
      opacity: 1;
    }
  }

  @keyframes puff-in-center {
    0% {
      -webkit-transform: scale(2);
      transform: scale(2);
      -webkit-filter: blur(4px);
      filter: blur(4px);
      opacity: 0;
    }

    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
      -webkit-filter: blur(0px);
      filter: blur(0px);
      opacity: 1;
    }
  }
}

@media only screen and (max-width: 1000px) {
  .about {
    margin-top: 35%;

    .text {
      flex-basis: 10%;
      font-family: Open Sans, sans-serif;
      font-size: 16px;
    }
  }
}
