@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@1&family=Skranji&display=swap');

.title_projects {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  margin-top: 10%;
  font-family: 'DM Serif Display', serif;
}

.projects {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;

  .projectsCollection {
    align-items: center;
    background-color: #fff;
    border: 5px solid #cecddc;
    border-radius: 25px;

    color: #3d4449;
    display: flex;
    flex-direction: column;
    height: auto;
    letter-spacing: 2px;
    margin-bottom: 5rem;
    min-height: 380px;
    min-width: 315px;
    cursor: pointer;
  }

  .projectsCollection:hover {
    transform: scale(1.08);
    box-shadow: 2px 5px 10px #f6457a;
    transition: transform 0.5s ease-in-out;
  }

  .projectsCollection img {
    width: 99%;
    height: 250px;
    object-fit: cover;
  }

  .projectsCollection h4 {
    color: #3d4449;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
  }

  .links_project {
    display: flex;
    flex-direction: row;
    gap: 5em;
  }

  .projectsCollection svg {
    margin-top: 30%;
    background-color: rgb(250, 145, 240);
    border-radius: 50px;
    color: white;
    display: flex;
    justify-content: center;
    height: 45px;
    width: 40px;
  }
}

.slide-in-elliptic-top-fwd {
  -webkit-animation: slide-in-elliptic-top-fwd 0.7s
    cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-in-elliptic-top-fwd 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    both;
}

