@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@1&family=PT+Serif&family=Roboto+Serif:ital,opsz,wght@1,8..144,100&family=Skranji&display=swap');

.topBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  width: 100%;
  z-index: 1;
  top: 0;
  height: 10vh;
}

.nav-links {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin-right: 50px;
}
.li .activeLink {
  border-bottom: 2px solid #dd9892;
  color: #dd9892;
}

.nav-links a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}

.nav-links li:hover {
  height: 40px;
  background-color: #e4dada;
}

.nav-links li.active {
  background-color: #e4dada;
}

.burger {
  font-size: 1.2em;
  display: none;
}

.menuList {
  display: flex;
  gap: 2em;
  font-size: 20px;
  font-family: 'Skranji', cursive;
  a {
    color: rgb(255, 255, 255);
    text-decoration: none;
  }
}

ul {
  color: black;
  list-style: none;
  border-bottom: none;
}

li {
  color: black;
}

@media only screen and (max-width: 1000px) {
  .topBar {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 115px;
    cursor: pointer;
    position: fixed;
    /* Agregado para que la lista se posicione correctamente */
  }

  .nav-links {
    display: block;
    text-align: center;
    margin-top: 50px;
  }

  .nav-links li {
    margin-bottom: 20px;
  }

  ul {
    list-style: none;
    font-size: 13px;
    font-family: Arial, Helvetica, sans-serif;
  }

  .burger {
    display: block;
    width: 50px;
    /* Ancho fijo del botón */
  }

  .menuList {
    display: none;
    left: 0;
    width: 100%;
  }

  .menuList.show {
    display: block;
  }

  .topBar__left {
    align-self: flex-start;
    /* Agregado para posicionar el botón a la izquierda */
    margin-left: 20px;
    /* Agregado para darle un poco de espacio */
  }

  .topBar__center {
    top: 0;
    position: fixed;
  }
}
