@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@1&family=Skranji&display=swap');

.title_tecnologies {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  font-family: 'DM Serif Display', serif;
}

.icons {
  display: flex;
  justify-content: center;
  gap: 3em;
  cursor: pointer;

  img {
    width: 50px;
    height: 50px;
  }
}

@media only screen and (max-width: 1000px) {
  .icons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
  }
}
