@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@1&family=Skranji&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@1&family=Roboto+Serif:ital,opsz,wght@1,8..144,100&family=Skranji&display=swap');

.portfolio {
  margin-top: 15%;
  display: flex;
  justify-content: center;
  align-items: center;

  .header_portfolio {
    flex-basis: 20%;
    text-align: center;
  }

  .img_portfolio {
    flex-basis: 20%;
    text-align: center;
  }

  .img_portfolio img {
    width: 275px;
    height: 320px;
    border-radius: 30%;
    object-fit: cover;
    cursor: pointer;
  }

  h1 {
    font-family: 'DM Serif Display', serif;
    font-weight: 700;
    font-size: 50px;
    letter-spacing: 2px;
    text-align: center;
    color: #fff;
  }

  h2 {
    font-family: 'DM Serif Display', serif;
  }
  .contact-info {
    font-size: 18px;
    font-weight: bold;
    margin-top: 20px;
  }

  button {
    cursor: pointer;
    border-radius: 10px;
    width: 162px;
    height: 36px;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    margin-right: 5%;
    background-color: #dd9892;
    margin-top: 5%;
  }

  a {
    text-decoration: none;
    color: black;
  }

  button:hover {
    background-color: #aaa8bd;
    transition: 0.8s;
  }

  .slide-in-left {
    -webkit-animation: slide-in-left 0.9s cubic-bezier(0.25, 0.46, 0.45, 0.94)
      both;
    animation: slide-in-left 0.9s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  }

  /* ----------------------------------------------
 * Generated by Animista on 2023-5-3 9:13:2
 * Licensed under FreeBSD License.
 * See http://animista.net/license for more info. 
 * w: http://animista.net, t: @cssanimista
 * ---------------------------------------------- */

  /**
 * ----------------------------------------
 * animation slide-in-left
 * ----------------------------------------
 */
  @-webkit-keyframes slide-in-left {
    0% {
      -webkit-transform: translateX(-1000px);
      transform: translateX(-1000px);
      opacity: 0;
    }

    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
      opacity: 1;
    }
  }

  @keyframes slide-in-left {
    0% {
      -webkit-transform: translateX(-1000px);
      transform: translateX(-1000px);
      opacity: 0;
    }

    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
      opacity: 1;
    }
  }
}

@media only screen and (max-width: 1000px) {
  .portfolio {
    margin-top: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    button {
      cursor: pointer;
      border-radius: 10px;
      width: 162px;
      height: 36px;
      font-family: 'Roboto', sans-serif;
      font-size: 20px;
      margin-right: 5%;
      background-color: #dd9892;
      margin-top: 5%;
    }

    .img_portfolio {
      text-align: center;
      margin-top: 5%;
    }
  }
}
